<template>
  <div>
    <v-list class="ml-5" v-for="item in articleList" :key="item.id">
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title style="cursor: pointer;">
            <b class="dots"></b>
            <span
              id="art_name"
              @click="handleToDetail(item.id)"
              class="black--text"
              >{{ item.title }}</span
            >
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-divider light></v-divider>
    </v-list>
    <div class="text-center">
      <v-pagination
        v-model="page"
        class="my-4"
        :length="pages"
        :total-visible="7"
        @input="getSubjectList()"
        @next="getSubjectList()"
        @previous="getSubjectList()"
      ></v-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: 'List',
  props: ['subject'],
  watch: {
    $route: 'getSubjectList'
  },
  data() {
    return {
      page: 1,
      pages: 1,
      articleList: []
    }
  },
  methods: {
    handleToDetail(id) {
      this.$router.push('/company/demand/detail/' + id)
    },
    async getSubjectList() {
      let res = await this.$http.get(
        '/api/company/remand?need=' + this.subject + '&page=' + this.page
      )
      if (res.data.code != 200) {
        this.articleList = res.data.data
        return false
      }
      this.articleList = res.data.data.enterpriseneeds.list
      this.pages = res.data.data.enterpriseneeds.pages
    },
    async getJxList() {
      let res = await this.$http.get(
        '/api/school/remand?need=机械' + '&page=' + this.page
      )
      if (res.data.code != 200) {
        this.articleList = res.data.data
        return false
      }
      this.articleList = res.data.data.enterpriseneeds.list
      this.pages = res.data.data.enterpriseneeds.pages
    }
  },
  mounted() {
    this.getJxList()
  }
}
</script>

<style scoped>
#art_name:hover {
  margin-left: 30px;
}
.dots {
  width: 4px;
  height: 4px;
  background-color: #003366;
  display: inline-block;
  margin-right: 20px;
}
</style>
